import {Generator} from "../../templator/generator";

export function templateButton (context) {
    const tree = {
        tag:"button",
        attributes: [
            {attr:"class", val:"{% classButton %}"},
            {attr:"type", val:"{% typeButton %}"},
        ],
        text: "{% textButton %}"
    };

    let textArea = new Generator(context);
    return textArea.template(tree);
}