import {extendsProfile} from "../index";

export function getProfileMain() {
    const context = {
        errorPageHead: "500",
        errorPageText: "Мы уже фиксим",
        inputs: [
            {
                idInput: "login_input",
                labelText: "Имя",
                nameInput: "first_name"
            },
            {
                idInput: "login_input",
                labelText: "Фамилия",
                nameInput: "second_name"
            },
            {
                idInput: "login_input",
                labelText: "Логин",
                nameInput: "login"
            },
            {
                idInput: "login_input",
                labelText: "E-mail",
                nameInput: "email"
            },
            {
                idInput: "login_input",
                labelText: "Пароль",
                nameInput: "password"
            },
            {
                idInput: "password_input",
                labelText: "Телефон",
                nameInput: "phone"
            }]
    };

    return extendsProfile(context);
}