import {getAuth} from "../index";

export function getAuthorization() {
    const context = {
        headPage: "Вход",
        inputs: [
            {
                idInput: "login_input",
                labelText: "Логин",
                nameInput: "login"
            },
            {
                idInput: "password_input",
                labelText: "Пароль",
                nameInput: "password"
            }],
        buttons: [
            {
                classButton: "btn-primary",
                typeButton: "button",
                textButton: "Войти",
            }
        ],
        links: [
            {
                hrefLink: "auth/authorization",
                textLink: "Нет аккаунта?"
            }
        ]

    };

    return getAuth(context);
}