import {Generator} from "../../templator/generator";
import {getButton} from "../../components/button";

export function templateModal(context) {
    const tree = {
        tag: "div",
        attributes: [
            {attr: "class", val: "div-modal"},
        ],
        nest: [
            {
                tag: "div",
                attributes: [
                    {attr: "class", val: "div-modal__modal"}
                ],
                nest: [
                    {
                        tag: "h3",
                        attributes: [
                            {attr: "class", val: "div-modal__modal_header"}
                        ],
                        text: "{% modalHeader %}"
                    },
                    {
                        tag: "a",
                        attributes: [
                            {attr: "class", val: "div-modal__modal_body"},
                            {attr: "href", val: "src/action"}
                        ],
                        text: "{% modalBody %}"
                    },
                    {
                        tag: "div",
                        attributes: [
                            {attr: "class", val: "div-modal__modal_footer"}
                        ]
                    },
                ]
            },
        ]
    };

    let modal = new Generator(context).template(tree);
    modal.querySelector('.div-modal__modal_footer').append(getButton(context.modalButton))

    return modal;
}