import {templateModal} from "./modal.tmpl";

export function getModal() {
    const context = {
        modalHeader: "Загрузите файл",
        modalBody: "Выбрать файл с компьютера",
        modalButton: {
            classButton: "btn-primary",
            typeButton: "button",
            textButton: "Поменять"
        }
    };

    return templateModal(context);
}