import {Generator} from "../../templator/generator";
import {getInput} from "../../components/input";
import {getButton} from "../../components/button";
import {getLink} from "../../components/link";
import {getProfileButtons} from "./components/buttons";
import {getProfileAvatar} from "./components/avatar";

export function templateProfile(context) {
    const tree = {
        tag:"section",
        attributes: [
            {attr:"class", val:"section-profile"}
        ]
    };
    let profile = new Generator(context);
    const tmplProfile = profile.template(tree);

    tmplProfile.prepend(getProfileAvatar());

    if (context){
        if (Array.isArray(context.inputs)){
            context.inputs.map(item => tmplProfile.append(getInput(item)));
        }
    }

    tmplProfile.append(getProfileButtons());

    return tmplProfile;
}