import {Generator} from "../../templator/generator";

export function templateErrors(context) {
    const tree = {
        tag: "section",
        attributes: [
            {attr: "class", val: "section-error"},
        ],
        nest: [
            {
                tag: "H1",
                text: "{% errorPageHead %}"
            },
            {
                tag: "span",
                text: "{% errorPageText %}"
            },
            {
                tag: "a",
                attributes: [
                    {attr: "href", val: "list_chats"}
                ],
                text: "Назад к чатам"
            },
        ]
    };

    let pageError = new Generator(context);
    return pageError.template(tree);

}