import {element} from "./element";

export class Generator {

    constructor(ElementContext) {
        this._ElementContext = ElementContext;
    }

    template(tree){
        return this._createElement(tree);
    }

    _createElement(tree){
        let newElement;
        newElement = element(this._ElementContext, tree);
        if (tree.nest){
            tree.nest.map(item => newElement.append(this._createElement(item)));
        }
        return newElement;
    }



}