import {Generator} from "../../../../templator/generator";
import {getButton} from "../../../../components/button";

export function templateProfileButtons(context) {
    const tree = {
        tag:"div",
        attributes: [
            {attr:"class", val:"section-profile__profile-buttons"},
        ]
    };

    let profileButton = new Generator().template(tree);
    context.map(item => profileButton.append(getButton(item)));

    return profileButton;
}