import {replaceBrace} from "./replace_brace";

export function element(context = false, element){
    const result = document.createElement(element.tag);

    if (element.attributes){
        let val = "";
        element.attributes.forEach(function (item) {
            val = replaceBrace(context, item.val);
            result.setAttribute(item.attr, val);
        })
    }
    if (element.text){
        result.innerText = replaceBrace(context, element.text);
    }
    return result;
}