import {getAuth} from "../index";

export function getRegistration() {
    const context = {
        headPage: "Регистрация",
        inputs: [
            {
                idInput: "login_input",
                labelText: "Имя",
                nameInput: "first_name"
            },
            {
                idInput: "login_input",
                labelText: "Фамилия",
                nameInput: "second_name"
            },
            {
                idInput: "login_input",
                labelText: "Логин",
                nameInput: "login"
            },
            {
                idInput: "login_input",
                labelText: "E-mail",
                nameInput: "email"
            },
            {
                idInput: "login_input",
                labelText: "Пароль",
                nameInput: "password"
            },
            {
                idInput: "password_input",
                labelText: "Телефон",
                nameInput: "phone"
            }
        ],
        buttons: [
            {
                classButton: "btn-danger",
                typeButton: "button",
                textButton: "Регистрация",
            }
        ]

    };

    return getAuth(context);
}