function identity(value) {
    return value;
}

function last(array) {
    if (Array.isArray(array)){
        return array[array.length - 1];
    }else {
        return undefined;
    }
}

function first(array) {
    if (Array.isArray(array)){
        return array[0];
    }else {
        return undefined;
    }
}

export function range(start = 0, end, step = 1, isRight = true) {
    let result = [];
    if (end === undefined){
        end = start;
        start = 0;
    }

    for (let n = Math.abs(start); n < Math.abs(end); n += Math.abs(step === 0 ? 1 : step)) {
        if (step === 0){
            result.push(start);
        }else {
            result.push(n);
        }
    }

    if (start < 0 || end < 0 || step < 0){
        result = result.map(item => item !== 0 ? -Math.abs(item) : item);
    }

    if (isRight){
        result = result.reverse();
    }
    return result;
}

export function isEmpty(variable){
    const type = typeof variable;
    if (variable == null || variable === Number || variable === Set || variable === Map || variable === Array || variable === Object) return true;
    if (type === 'boolean') return true;
    if (type === 'string') return !variable;
    if (type === 'number') return true;
    if (Array.isArray(variable)) return !variable.length;
    if (type === 'object') return !Object.keys(variable).length;
    return !variable;
}

