import {getContext} from "./get_context";

export function replaceBrace(context, str) {
    const TEMPLATE_REGEXP = /\{%(.*?)%}/gi;
    const regEx = RegExp(TEMPLATE_REGEXP, 'gi');
    let ctx = null;
    if (context){
        while ((ctx = regEx.exec(str))) {
            if (ctx[1]) {
                str = str.replaceAll(new RegExp(ctx[0], "gi"), getContext(context, ctx[1].trim(), null));
            }
        }
    }

    return str;
}