import {Generator} from "../../../../templator/generator";

export function templateProfileAvatar(context) {
    const tree = {
        tag: "div",
        attributes: [
            {attr: "class", val: "div-avatar"}
        ],
        nest: [
            {
                tag: "div",
                attributes: [
                    {attr: "class", val: "div-avatar__container-img"}
                ],
                nest: [
                    {
                        tag: "img",
                        attributes: [
                            {attr: "img", val: "{% profileAvatar %}"},
                            {attr: "alt", val: "{% avatarAlt %}"}
                        ]
                    }
                ]
            },
            {
                tag: "a",
                attributes: [
                    {attr: "href", val: "qwer"}
                ],
                text: "Сменить фото"
            }
        ]
    };
    return new Generator(context).template(tree);
}