import * as classes from "./index.scss";
import {getAuthorization} from "./pages/auth/authorization";
import {getRegistration} from "./pages/auth/registration";
import {getPage500} from "./pages/errors/500";
import {getPage404} from "./pages/errors/404";
import {getProfileMain} from "./pages/profile/main";
import {getModal} from "./modules/modal";
import {isEmpty, range} from "./utils/identity";


const main = document.querySelector('main');

document.querySelector('body').className = "scroll scroll-bold";


window.addEventListener('DOMContentLoaded', function () {
    const navigation = document.querySelectorAll('header a');
    navigation.forEach(function (item) {
        item.onclick = function (e) {
            e.preventDefault();
            viewPage(this.getAttribute('href'));
        }
    })
    let currentUrl = ((window.location.pathname).trim()).replace('/', '');
    viewPage(currentUrl);
});

function viewPage(url) {
    url = ((url).trim()).replace('/', '')
    main.innerHTML = "";
    switch (url) {
        case "registration":
            main.append(getRegistration());
            break;
        case "authorization":
            main.append(getAuthorization());
            break;
        case "profile":
            main.append(getProfileMain());
            break;
        case "404":
            main.append(getPage404());
            break;
        case "500":
            main.append(getPage500());
            break;
        case "modal":
            main.append(getModal());
            break;
        default:
            main.append(getPage404());

    }
}

