import {templateProfileButtons} from "./profile_buttons.tmpl";

export function getProfileButtons() {
    const context = [
        {
            classButton: "btn-primary",
            typeButton: "button",
            textButton: "Редактировать",
        },
        {
            classButton: "btn-primary",
            typeButton: "button",
            textButton: "Сменить пароль",
        },
        {
            classButton: "btn-danger",
            typeButton: "button",
            textButton: "Выход",
        }
    ];
    return templateProfileButtons(context);
}