import {Generator} from "../../templator/generator";

export function templateInput (context) {
    const tree = {
        tag:"div",
        attributes: [
            {attr:"class", val:"div_input"}
        ],
        nest:[
            {tag:"label",
                attributes: [
                    {attr:"for", val:"{% idInput %}"}],
                text: "{% labelText %}"},
            {tag:"input",
                attributes: [
                    {attr:"id", val:"{% idInput %}"},
                    {attr:"name", val:"{% nameInput %}"}
                ]
            }
        ]
    };

    let textArea = new Generator(context);
    return textArea.template(tree);
}