import {Generator} from "../../templator/generator";
import {getInput} from "../../components/input";
import {getButton} from "../../components/button";
import {getLink} from "../../components/link";

export function templateAuth(context) {
    const tree = {
        tag:"section",
        attributes: [
            {attr:"class", val:"section-auth"}
        ],
        nest: [
            {tag: "H1", text: "{% headPage %}"}
        ]
    };
    let blockAuthorization = new Generator(context);
    const auth = blockAuthorization.template(tree);

    if (context){
        if (Array.isArray(context.inputs)){
            context.inputs.map(item => auth.append(getInput(item)));
        }
        if (Array.isArray(context.buttons)){
            context.buttons.map(item => auth.append(getButton(item)));
        }
        if (Array.isArray(context.links)){
            context.links.map(item => auth.append(getLink(item)));
        }
    }

    return auth;
}